@import './_mixins';
@include font-face('DS-Digital', './fonts/DS-Digital');

body {
  margin: 0;
  padding: 0;
  -webkit-tap-highlight-color: transparent;
  user-select: none;
  background-color: #d9dbc5;
}

.root {
  width: 100%;
  height: 100%;
  line-height: 0;
  padding-top: .5vh;
}

.path {
  fill: inherit;
  fill-rule: evenodd;
}